body{
  background-color: #FEFBF6;
}
.title{
  text-align: center;
  /* font-size: larger; */
  color: #3D3C42;
  font-family: 'Mochiy Pop One', sans-serif;
  
}
.MySkills{

  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}
.cards-container {
    position: relative;
    margin: 0;
    padding: 0;

    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.gif{
    width: 200px;
    height: 200px;
}
.icon{
  width: 100px;
  margin-top: 20%;
}
.SkillCard {
  position: relative;
  margin-top: 5%;
  margin-right: 2.5%;
  margin-left: 2.5%;
  width: 200px;
  height: 200px;
  perspective: 200px;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
  transition: transform 1s;
  transform-style: preserve-3d;
}

.SkillCard:hover .content {
  transform: rotateY( 180deg ) ;
  transition: transform 0.5s;
}

.front,
.back {
  position: absolute;
  height: 100%;
  width: 100%;
  background:  #A6D1E6;
  line-height: 200px;
  color: #256D85;
  text-align: center;
  
  border-radius: 5px;
  backface-visibility: hidden;
}

.back {
  background: #FF1E00;
  color: #FF1E00;
  transform: rotateY( 180deg );
}